@import url(https://fonts.googleapis.com/css2?family=Fugaz+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500&display=swap);
.submit-button {
  background-color: #df629c !important;
  border-color: #df629c !important;
  width: 100%;
}

.search-title,
.search-city {
  color: #5576ce;
  font-size: 30px;
  margin-top: 2px;
}

.search-city {
  margin-top: -4px;
  margin-left: 7px;
}

.form-group {
  margin: 0px !important;
}

.navigation {
  margin-top: 10px;
}

select {
  border: 1px solid #ccc;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url("data:image/svg+xml,%3Csvg width='1em' height='1em' viewBox='0 0 16 16' class='bi bi-chevron-double-down' fill='white' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' d='M1.646 6.646a.5.5 0 0 1 .708 0L8 12.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3E%3Cpath fill-rule='evenodd' d='M1.646 2.646a.5.5 0 0 1 .708 0L8 8.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3E%3C/svg%3E")
    94% / 8% no-repeat #eee;
  border-radius: 4px;
  font-family: "Oswald", sans-serif !important;
  padding: 2px 0px;
  color: white;
  background-color: #17a2b8;
  text-align: center;
  font-size: 22px !important;
  width: 300px;
}

select:focus {
  outline: none;
}

.business-container {
  width: 410px;
  margin-top: 10px;
  margin-right: 10px;
  cursor: pointer;
  height: 73vh;
  overflow: scroll;
  border: none;
}

@media (min-height: 700px) {
  .business-container {
    height: 75.5vh;
  }
}

.map-service-box:hover .business-name,
.map-service-box.hover .business-name {
  background-color: #d42757;
}

.business-map-wrapper {
  margin-left: 40px;
  margin-right: 40px;
}

.no-address-list {
  height: 87.7vh;
  overflow: auto;
}

.business-name {
  font-size: 18px;
  background-color: rgb(23, 162, 184);
  padding: 10px;
  color: white;
  font-weight: 600;
  border-radius: 10px 10px 0px 0px;
}

.business-details-wrapper {
  padding: 10px;
}

.business-details {
  font-size: 14px;
  margin-bottom: 5px;
}

.dropdown-and-toggle {
  background-color: lightgray;
  border-radius: 4px;
  height: 39px;
  width: 384px;
  margin: auto;
}

.dropdown-background {
  background-color: #ecf3f4;
}

.map-service-box {
  border: 1px solid darkgray;
  border-radius: 12px;
  margin-bottom: 5px;
  background-color: #ecf3f4;
}

.home-container {
  margin-left: 45px;
  margin-right: 45px;
  margin-top: -10px;
}

.home-left-image {
  width: 347px;
  min-width: 347px;
  margin-right: 20px;
  max-width: 25%;
}

.home-text-wrapper {
  font-size: 36px;
  margin-top: 40px;
  margin-right: 40px;
}

.home-title-wrapper {
  margin-bottom: 45px;
}

.home-title-first {
  font-family: Fugaz One, sans-serif;
  color: #508ea6;
  margin-bottom: 30px;
}

.home-location-text {
  font-family: Fugaz One, sans-serif;
  color: #d42757;
}

.home-categories-wrapper {
  font-size: 16px;
  max-width: 750px;
}

.home-category-title {
  font-size: 24px;
  margin-bottom: 10px;
}

.home-category-services {
  font-size: 16px;
}

.home-service {
  cursor: pointer;
  margin-bottom: 4px;
}
.home-service:hover {
  color: #d42757;
}

.home-vertical-divider {
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 10px;
  width: 1px;
  height: 190px;
  background-color: lightgray;
}

@media (max-width: 999px) {
  .home-left-image {
    display: none;
  }
}

@media (min-width: 1500px) {
  .home-left-image {
    width: 500px;
  }
}

.about-container {
  margin-left: 45px;
  margin-right: 45px;
}

.about-title {
  color: rgb(181, 179, 179);
  text-shadow: 5px 5px 5px lightgray;
}

.about-left-image {
  width: 215px;
  min-width: 215px;
  background-color: gray;
  margin-right: 20px;
}

.request-button {
  border: 1px solid #e8a63c;
  color: #e8a63c;
  outline: none;
  font-weight: 300;
  border-radius: 10px;
  background-color: transparent;
  font-size: 24px;
}

.request-button:focus {
  outline: none;
}

.request-button:hover {
  color: white;
  background-color: #e8a63c;
  outline: none;
}

.about-text-wrapper {
  color: #508ea6;
  font-size: 20px;
  text-align: justify;
  text-justify: auto;
}

.contact-container {
  margin-left: 45px;
  margin-right: 45px;
}

.contact-title {
  color: rgb(181, 179, 179);
  text-shadow: 5px 5px 5px lightgray;
}

.contact-left-image {
  width: 215px;
  min-width: 215px;
  background-color: gray;
  margin-right: 20px;
}

.contact-text-wrapper {
  text-align: justify;
  text-justify: auto;
  font-size: 28px;
}

.contact-hear {
  color: #d42757;
  text-shadow: 5px 5px 5px lightgray;
}

.contact-text {
  font-size: 20px;
  color: #508ea6;
}

.contact-button {
  border: 1px solid #e8a63c;
  color: #e8a63c;
  outline: none;
  font-weight: 300;
  border-radius: 10px;
  background-color: transparent;
  font-size: 24px;
}

.contact-button:focus {
  outline: none;
}

.contact-button:hover {
  color: white;
  background-color: #e8a63c;
  outline: none;
}

.faq-container {
  margin-left: 45px;
  margin-right: 45px;
}

.faq-title {
  color: rgb(181, 179, 179);
  text-shadow: 5px 5px 5px lightgray;
}

.faq-left-image {
  width: 215px;
  min-width: 215px;
  height: 300px;
  background-color: gray;
  margin-right: 20px;
}

.faq-text-wrapper {
  text-align: justify;
  text-justify: auto;
  font-size: 28px;
}

.faq-question {
  color: #d42757;
  text-shadow: 5px 5px 5px lightgray;
  margin-bottom: 0px;
}

.faq-answer {
  color: #508ea6;
  font-size: 20px;
}

.navbar-content {
  margin-top: 2em;
}

.nav-button {
  color: darkgrey !important;
}

.nav-button:hover,
.nav-button:visited,
.nav-button:active {
  color: darkslategray !important;
  text-decoration: none !important;
}

.initial-logo {
  width: 145px;
  margin-top: -15px;
  margin-bottom: -10px;
}

.header-container {
  padding: 20px 45px 0px 45px;
}

.header-divisor {
  margin: 10px 45px;
}

.social-medias {
  width: 32px;
}

#left-side {
  margin-top: 15px;
}

#left-side,
#right-side {
  width: 233px;
}

/* Add some App.js styles here that you wanna see applied throughout the app */

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 30px;
  line-height: 60px;
  background-color: lightgray;
}

body {
  background-color: white;
  font-family: Quicksand, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  color: #404247;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

